import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import queryString from 'query-string';

const ProcessingQueryStr = (props) =>{

    const search = useLocation().search;
    const query = queryString.parse(search);
    useEffect(()=>{
    props.setYourLivePrefecture(query['yourLivePrefecture']);
    props.setComparePrefecture(query['comparePrefecture']);
    props.setSalary(query['salary'] )
    },[]);

};

export default ProcessingQueryStr;