const Form = (props) =>{

    const todoufuken =props.todoufuken;
    return(
    <form>
        <p id="your_salary">あなたの年収(税引き前年収)<br/>
        <input type ="number" name="salary" placeholder="半角数字" onChange={e =>props.setSalary(e.target.value)}/>万円
        </p>

        <div id="select_prefecture">
            <p id="detail_prefecture">あなたの住んでいる都道府県名<br/>
            <select size="7" name="yourLivePrefecture" onChange={e =>props.setYourLivePrefecture(e.target.value)} onClick={props.getWeather}>
                {Object.keys(todoufuken).map(key=><option value={key}>{todoufuken[key].name}</option>)}
            </select>
            </p>
        


            <p id="detail_prefecture">比較したい都道府県<br/>
                <select size="7" name="comparePrefecture" onChange={e =>props.setComparePrefecture(e.target.value)}>
                    {Object.keys(todoufuken).map(key=><option value={key}>{todoufuken[key].name}</option>)}
                </select>
            </p>
        </div>

    </form>
    );
};

export default Form;