import {
    FacebookShareButton,
    FacebookIcon,
    TwitterShareButton,
    TwitterIcon
} from 'react-share';

const Calc = (props) =>{
    
    //受け取ったpropsは整数のため、都道府県に変換する処理
    let yourLivePrefProps = props.todoufuken[props.yourLivePrefecture];
    let comparePrefProps = props.todoufuken[props.comparePrefecture];

    //比較給与を計算する処理
    let compareSalary;
    if(comparePrefProps && yourLivePrefProps){
        let yourLivePrefDiff = props.salary/yourLivePrefProps.salary;
        compareSalary = parseFloat(comparePrefProps.salary * yourLivePrefDiff).toFixed(1);
    }

    //share用のURL生成
    let anchor = document.createElement("a");
    anchor.href = `?salary=${props.salary}&yourLivePrefecture=${props.yourLivePrefecture}&comparePrefecture=${props.comparePrefecture}`;

    //share用の文言生成
    const shareText =`${yourLivePrefProps ? yourLivePrefProps.name :"XX県"}での年収${props.salary ? props.salary:" XXX " }万円は${comparePrefProps ? comparePrefProps.name:"XX県"}だと年収${compareSalary ? compareSalary:"XXX"}万円です。自分の年収を、他の地域の年収と比較しよう！`

    return(

        <div id="calc_result_main">
            <p className="compare_result">比較結果</p>
            あなたの住んでいる都道府県{yourLivePrefProps && <span className="strong_result">{yourLivePrefProps.name}</span>}での<p className="strong_result">年収{props.salary ? props.salary:" XXX " }万円は</p>
            比較対象の都道府県{comparePrefProps && <span className="strong_result">{comparePrefProps.name}</span>}だと
            <p className="strong_result">年収{compareSalary ? compareSalary:"XXX"}万円</p>です。
            <div id="share">
                <FacebookShareButton url={[anchor.href]} quote={[shareText]}>
                    <FacebookIcon size={32} round />
                </FacebookShareButton>
                <TwitterShareButton url={[anchor.href]} title={[shareText]}>
                    <TwitterIcon size={32} round />
                </TwitterShareButton>
            </div>

            <div className="bottom_caution">
                <p>本結果は、令和３年賃金構造基本統計調査https://www.mhlw.go.jp/toukei/itiran/roudou/chingin/kouzou/z2021/index.html
より、各地域の平均値と入力した年収を比較し、その乖離を元に計算したものです。<br/>
                結果の正確性を保証するものではなく、参考程度にご使用いただくことを推奨します</p>
            </div>
        </div>
    );
};

export default Calc;